	module.exports = {"data":{"markdownRemark":{"html":"<p>I recently rooted my htc desire.\nI originally hadn’t felt the need to do so, it came with froyo and even had a wireless tethering app preloaded, something that would have needed root permissions to install myself.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 180px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 166.66666666666669%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"htc desire\"\n        title=\"\"\n        src=\"/static/5a6457e0bc73f4b5d3ea85c9fe1fdbd1-5883e.png\"\n        srcset=\"/static/5a6457e0bc73f4b5d3ea85c9fe1fdbd1-fa99a.png 148w,\n/static/5a6457e0bc73f4b5d3ea85c9fe1fdbd1-5883e.png 180w\"\n        sizes=\"(max-width: 180px) 100vw, 180px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>I’m not even sure why I wanted to root it in the first place, it wasn’t to allow piracy seeing as on android you can easily pirate from the get go without needing to hack your device, not that there’s anything worth pirating anyways.</p>\n<p>But now that I’m root I loaded a custom rom and the difference is spectacular, a horde of tweaks to make everything act how you want is great and the optimization that has been done is amazing and of course the removal of manufacturer bloatware is a god send.</p>\n<p>Now to get to the point of this post.</p>\n<p>People generally complain about the iphone being too locked down and that you can’t customize anything unless you jailbreak it. Which is true in a way, not so much recently with all the updates and added features that have come to ios over the years. My problem is that people say “oh unless you jailbreak it, it’s no good”, well when you root your android phone you’re doing the exact same thing! Jailbreaking is gaining root access! And any android user knows you can’t do anything unless you root it.</p>\n<p>But it doesn’t end there, not only do we all do the same thing to get the most out of our devices but jailbreaking an ios device has always been infinitely easier than rooting an android phone, at one point just by visiting a website you could get your iphone jailbroken!</p>\n<p>But also the ios scene feels so far ahead of android, just through examples like cydia and installous, central ways to find everything you can add to your jailbroken device, whereas on android you have to crawl google for tutorials or discover websites and visit them repeatedly to find out what’s new.</p>\n<p>I just think it’s great how not only is ios the easier and slicker os, but so is there entire hacking scene!</p>\n<p>Sorry but this has been quite a ramble but I’m on the train and felt like putting something out.</p>\n<p>P.S. This was typed entirely on my phone so pardon the mistakes.</p>\n<p>Update: I didn’t mention that I hadn’t rooted sooner because htc had promised that the latest version of android 2.3 gingerbread was coming to the desire by July so I thought it best to wait and have a more stable device. Today though, htc announced that they had scrapped plans to bring the update citing the device not having enough memory for the latest version. Just goes to show again the “benefits” of being open…</p>\n<p>Update 2: Well htc went back on themselves and apparently will release gingerbread for the desire, who knows when, if ever.</p>","frontmatter":{"date":"June 13, 2011","path":"/root-of-the-problem.html","tags":["tag"],"title":"Root of the problem","image":{"childImageSharp":{"resize":{"src":"/static/4e9a95cf7025f7dc1da351e45e9f97b3-57c0c.jpg"}}}}}},"pathContext":{"prev":{"excerpt":"It won’t be long until I’ll seriously consider disconnecting from the internet and never coming back for fear of imprisonment. Nearly all the isps that have instated 3 strikes and you’re out policies are under investigation after all the people who…","html":"<p>It won’t be long until I’ll seriously consider disconnecting from the internet and never coming back for fear of imprisonment.</p>\n<ul>\n<li>\n<p>Nearly all the isps that have instated 3 strikes and you’re out policies are under investigation after all the people who received such warnings had never file shared in their life, don’t leave your wifis open people.</p>\n</li>\n<li>\n<p>A Canadian was fined 60.000$ for sharing 2 adult films online….2.</p>\n</li>\n<li>\n<p>The British admin of tv shack, a link sharing site not actually hosting any infringing content and probably not even one of the uploaders, is going to be extradited to the U.S. to be charged there….extradited, I remember when that word only came up with terrorists or mastermind thieves not 23 year old students running a site that doesn’t host any infringing material.</p>\n</li>\n<li>\n<p>Someone was fined 80.000$ when he downloaded an adult movie disguised as out of copyright classical music, something that has happened to all of us I’m sure, begs to ask who posted the disguised file in the first place.</p>\n</li>\n<li>\n<p>In the U.S. they’re hoping to pass a law that will make it a felon to embed copyrighted content, see a video you like on youtube and you embed it on your blog which then turns out to be infringing copyright and that will be a felon, do some graffiti, go skateboarding and run a blog and you’re looking at life.</p>\n</li>\n</ul>\n<p>Now the scary thing is these stories are from this week alone.</p>\n<p>And the worst part is all the lawyers and companies carrying out these acts are all linked in some way to having commited piracy themselves or to be operating outside the law.</p>\n<p>If you ever receive a letter demanding money because you allegedly infringed copyright, it was probably sent by a firm like Davenport Lyons who were just found guilty of professional misconduct so the tides may be changing on that front but when the notorious acs law firm shuts it’s doors after being threatened with a possible 600.000 pound fine if found guilty just to have it lowered to 800 pounds seeing as the owner now doesn’t have the means for a larger fine, the same owner who goes home to his 700.000 pound house and 2 luxury cars.</p>\n<p>It looks like a scary future for internet users as long as it continues to be unknown what is ok and what will land us in jail.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 580px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 31.03448275862069%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"copyright\"\n        title=\"\"\n        src=\"/static/75821e2571f287b5eca46fd5ec65e32b-7ee4f.png\"\n        srcset=\"/static/75821e2571f287b5eca46fd5ec65e32b-785d5.png 148w,\n/static/75821e2571f287b5eca46fd5ec65e32b-2f066.png 295w,\n/static/75821e2571f287b5eca46fd5ec65e32b-7ee4f.png 580w\"\n        sizes=\"(max-width: 580px) 100vw, 580px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Thanks to <a href=\"http://mimiandeunice.com/\">mimiandeunice</a> for the great comic and their ♡copyheart stance meaning I didn’t even have to ask permission, sharing is caring.</p>","id":"/Users/markholland/Documents/js/gatsby/partiallogic-gatsbyJS/posts/2011-06-21-just-an-average-internet-criminal/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2011-06-21T12:00:00+00:00","path":"/just-an-average.html","tags":["tag"],"title":"Just an average internet criminal"}},"next":{"excerpt":"I’m not anti-apple, not even in the slightest. I’ve owned apple products ever since the first iPod Shuffle. And apart from suddenly having to use a specific program to put music on my mp3 player it was a great experience, good battery life, decent…","html":"<p>I’m not anti-apple, not even in the slightest. I’ve owned apple products ever since the first iPod Shuffle.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 259px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 115.83011583011584%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"ipod shuffle\"\n        title=\"\"\n        src=\"/static/0d15083736851be7850f21d00b6f1227-34b47.png\"\n        srcset=\"/static/0d15083736851be7850f21d00b6f1227-b99f0.png 148w,\n/static/0d15083736851be7850f21d00b6f1227-34b47.png 259w\"\n        sizes=\"(max-width: 259px) 100vw, 259px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>And apart from suddenly having to use a specific program to put music on my mp3 player it was a great experience, good battery life, decent audio quality, well built and stylish at the time.</p>\n<p>It wasn’t long until my music library was growing way too fast for that little stick of white plastic to handle so I was lucky enough to be able to move up to the then called “ipod video” and my satisfaction in apple products grew even more.</p>\n<p>Since then I’ve had an ipod classic, 2 generations of ipod touchs and a more recent gen shuffle, but the adoption doesn’t end with me, my whole family now owns their own ipod touch and several gens of ipod to choose from for music, we even took the leap last year with the ipad.</p>\n<p>Now what’s blatantly missing from this love affair is any mention of mac computers and that leads me to the point of this post. Finally I’m able to give a decent opinion of the mystery behind apples computers.</p>\n<p>So what changed? well nothing really, I’d never buy a mac, no matter how justifiable the price is, so I haven’t. It turns out my library at uni has a program for allowing students free loaning of laptops and for whatever absurd reason think it’s necessary for half of those to be macs.</p>\n<p>So after just 4 days on the waiting list I was granted my macbook for 15 days, the current gen white macbook to be exact.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 300px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 64.33333333333333%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"unibody macbook\"\n        title=\"\"\n        src=\"/static/2ff415d46386395c70be7b44239b95c0-adfc8.png\"\n        srcset=\"/static/2ff415d46386395c70be7b44239b95c0-b4fb9.png 148w,\n/static/2ff415d46386395c70be7b44239b95c0-ab48e.png 295w,\n/static/2ff415d46386395c70be7b44239b95c0-adfc8.png 300w\"\n        sizes=\"(max-width: 300px) 100vw, 300px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>I’m not going to go into much detail but needless to say I was quickly impressed, It’s all about the subtleties that I won’t bother mentioning seeing as how if you own a mac you already know what I’d say and if you’re a non-user I doubt anything anyone could say would sway you towards osx.</p>\n<p>What I am going to mention though is that this seems like the best generation of macbook for me to have stumbled across, the new trackpad really does make all the difference, effortlessly gliding around while web browsing brings a whole new level of satisfaction. The unibody construction gives you the feeling of one solid piece of kit that you interact with, no annoying creaks to be heard here. The smaller screen, yet higher resolution than most 15.4″ pc laptops shows there’s no skimping on the spec.</p>\n<p>All in all I can understand why people choose to spend their money on macs, it’s not a decision I personally would make, I’d never buy the most expensive car, the most expensive tv, the most expensive fridge so why should I buy the most expensive computer? But I now see macs as the best computers partly justifying why they’re the most expensive.</p>\n<p>And so here I leave you, I have to return this tomorrow and go back to my Windows 7 netbook which works perfectly fine but just lacks that <em>grimace</em> “Magic”.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 580px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 125.51724137931035%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"doghousediaries apple\"\n        title=\"\"\n        src=\"/static/247c48de8b19e91e0e2c82c0ec410ed3-7ee4f.png\"\n        srcset=\"/static/247c48de8b19e91e0e2c82c0ec410ed3-785d5.png 148w,\n/static/247c48de8b19e91e0e2c82c0ec410ed3-2f066.png 295w,\n/static/247c48de8b19e91e0e2c82c0ec410ed3-7ee4f.png 580w\"\n        sizes=\"(max-width: 580px) 100vw, 580px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Big thanks to <a href=\"http://www.thedoghousediaries.com/\">The Doghouse Diaries</a> for letting me use the comic strip!</p>","id":"/Users/markholland/Documents/js/gatsby/partiallogic-gatsbyJS/posts/2011-06-06-fruity-computing/index.md absPath of file >>> MarkdownRemark","timeToRead":2,"frontmatter":{"date":"2011-06-06T12:00:00+00:00","path":"/fruity-computing.html","tags":["tag"],"title":"Fruity computing"}}}}

